.card {
  padding: 0 30px;
  width: 100%;
  .card-img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  .card-img-height-fixed {
    max-height: 600px;
  }
  .card-title {
    @extend .text-bold;
    font-size: 20px;
    margin: 7px 0;
  }
  .card-list {
    list-style: none;
    padding-left: 25px;
    margin: 15px 0;
    li {
      &:before {
        content: "\2022";
        color: $color-helper6;
        font-weight: bold;
        display: inline-block;
        width: 25px;
        margin-left: -25px;
      }
    }
  }
}

.card-column {
  display: flex;
  align-items: flex-start;
  padding: 25px 15px 0;
  margin: 0 -15px;
  .card-title {
    margin: 14px 0 0 0;
  }
  .column {
    padding: 0 15px;
    img {
      max-width: 100px;
    }
    p {
      margin-top: 2px;
    }
  }
}

.slider-service {
  .card-column {
    padding: 20px 15px 0;
    .column {
      .card-title {
        font-size: 18px;
        margin-top: 17px;
      }
      img {
        max-width: 60px;
        vertical-align: middle;
      }
      p {
        margin-top: 2px;
      }
    }
  }
}

.logos-img {
  margin: 35px 0 30px;
}

.bg-important-card {
  width: 220px;

  p {
    font-size: 24px;
    line-height: 22px;
    @extend .text-bold;
  }
}

.card-row {
  padding: 0;
  text-align: center;
  img  {
    max-width: 60px;
  }
  p {
    font-size: 14px;
  }
}
