.form {
  height: 100%;
  width: 100%;
  padding: 0 0 0 25px;
  .input-name {
    display: block;
    font-size: 16px;
    margin-bottom: 18px;
  }
  h5 {
    font-size: 24px;
    @extend .text-regular;
    margin: 30px 0 0;
  }
  h6 {
    font-size: 16px;
    font-weight: normal;
    margin-top: 0;
    margin-bottom: 18px;
  }
  input,
  textarea {
    width: 100%;
    @extend .text-light;
    color: $color-helper4;
    font-size: 16px;
    padding: 12px 20px;
    background-color: $color-helper2;
    border: 1px solid $color-helper2;
    border-radius: 3px;
    outline: none;
    transition: 300ms ease-out, 300ms ease-out;
    &::placeholder {
      transition: 300ms ease-out, 300ms ease-out;
      color: $color-helper1;
    }
    &:hover::placeholder {
      color: $color-helper4;
    }
    &:focus {
      outline: none;
      border-color: $color-helper12;
    }
    &.error {
      background-color: $color-helper13;
    }
    &.valid {
      background-color: $color-helper14;
    }
  }
  label.error {
    @extend .text-light;
    display: block;
    font-size: 16px;
    color: $color-helper15;
    margin-top: 15px;
  }
  .row {
    margin-top: 25px;
    margin-bottom: 18px;
  }
  &.waiting:after {
    background-color: $color-helper3;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .5;
    z-index: 600;
  }
}

.form-wrapper {
  position: relative;
  height: 100vh;
  padding-bottom: 50px;

  .scrollbar-wrapper {
    height: 100%;
    width: 100%;
    overflow-y: scroll;
  }
  .content-wrapper {
    width: 94%;
    height: 100%;
  }
}

.form-popup {
  display: none;
  background-color: $color-helper3;
  box-shadow: 0 0 8px 0 rgba(21, 21, 21, 0.1);
  padding: 27px 15px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 700;
  width: 100%;
  &.open {
    display: block;
  }
  .form-popup-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }
  h5 {
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 14px;
  }
  p {
    font-size: 16px;
  }
  .navigation {
    top: -25px;
    right: 0;
    z-index: 400;
    background-color: unset;
    box-shadow: unset;
  }
}

.logout {
  display: none;
  position: absolute;
  min-width: 320px;
  top: 15px;
  right: 13px;
  z-index: 500;
  padding: 25px 17px;
  background-color: $color-helper3;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  &.open {
    display: block;
  }
  h5 {
    @extend .text-regular;
    margin-top: 0;
    margin-bottom: 6px;
    font-size: 18px;
  }
  p {
    margin-bottom: 14px;
    margin-top: 0;
    font-size: 16px;
  }
  .navigation {
    top: -25px;
    right: -15px;
    z-index: 400;
    background-color: unset;
    box-shadow: unset;
  }
}
