/* General styles for all types of buttons */
.button {
  @extend .text-light;
  position: relative;
  display: inline-block;
  padding: 15px 20px;
  outline: none;
  border: none;
  cursor: pointer;
  background: $color-helper20;
  color: $color-helper4;
  border-radius: 3px;
  font-size: 16px;
  line-height: 18px;
  transition: 300ms ease-out, 300ms ease-out;
  &.button-lg {
    padding: 10px 20px;
  }
  &.button-sm {
    padding: 5px 13px;
  }
}

.button[disabled] {
  opacity: .5;
  cursor: default;
}

.button:hover {
  background-color: $color-helper16;
}

.button:active {
  background-color: $color-helper17;
}

.progress-button[disabled],
.progress-button[disabled].state-loading {
  cursor: default;
}

.progress-button.state-loading {
  background: $color-helper21;
}

.progress-button .content {
  position: relative;
  display: block;
}

.progress-button .content::before,
.progress-button .content::after  {
  position: absolute;
  right: 20px;
  color: $color-helper18;
  opacity: 0;
  -webkit-transition: opacity 0.3s 0.3s;
  transition: opacity 0.3s 0.3s;
}

.notransition {
  -webkit-transition: none !important;
  transition: none !important;
}

.progress-button .progress {
  background: $color-accent;
}

.progress-button .progress-inner {
  position: absolute;
  left: 0;
  background: $color-accent;
}

.progress-button[data-horizontal] .progress-inner {
  top: 0;
  width: 0;
  height: 100%;
  -webkit-transition: width 0.3s, opacity 0.3s;
  transition: width 0.3s, opacity 0.3s;
}

.progress-button[data-vertical] .progress-inner {
  bottom: 0;
  width: 100%;
  height: 0;
  -webkit-transition: height 0.3s, opacity 0.3s;
  transition: height 0.3s, opacity 0.3s;
}

/* Individual styles */
/* Choose the effect(s) you want, delete the rest */

/* top-line */
/* ====================== */

.progress-button[data-style="top-line"] .progress-inner {
  height: 3px;
}

.progress-button[data-style="top-line"] .content::before,
.progress-button[data-style="top-line"] .content::after {
  right: auto;
  left: 100%;
  margin-left: 25px;
}

.button.fixed-bottom {
  box-shadow: 0 0 8px 0 rgba(21, 21, 21, 0.1);
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}
