.container {
  padding-bottom: 70px;
  @media (max-width: 1020px) {
    padding-bottom: 0;
  }
}

.table-wrapper {
  padding: 0 30px 0;
  .table-container {
    width: 100%;
    border: 1px solid $color-helper11;
    border-radius: 5px;
  }
}

table {
  width: 100%;
  font-size: 16px;
  font-weight: normal;
  th {
    font-weight: normal;
    background-color: $color-helper9;
  }
}

table, th, td {
  vertical-align: top;
  font-size: 16px;
  line-height: 27px;
  border-collapse: collapse;
}

th {
  @extend .text-regular;
}

th, td {
  padding: 5px 10px;
  text-align: left;
}

table tbody tr:not(:last-child) {
  border-bottom: 1px solid $color-helper11;
}

table tr td:not(:last-child),
table tr th:not(:last-child) {
  border-right: 1px solid $color-helper11;
}

.table-sm {
  table, th, td {
    font-size: 14px;
    line-height: 23px;
  }
}


