.dropdown-menu {
  position: absolute;
  top: 10px;
  right: 0;
  left: 0;
  margin: auto;
  width: 195px;
  z-index: 700;
  li {
    font-size: 14px;
    &:before {
      display: none;
    }
  }
  ol {
    list-style:none;
    margin:0;
    padding:0;
    display:block;
    max-width:none;
    text-align:center;
    z-index:400;
    > li.active-block {
      background: $color-helper3;
      margin-bottom:1px;
      position:relative;
      transition:.5s;
      border-radius: 5px;
      box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.08);
      .active-item {
        display: flex;
        align-items: center;
        justify-content: center;
        color: $color-helper1;
        position:relative;
        text-decoration:none;
        padding: 6px 10px 10px 10px;
        .arrow img {
          display: block;
          width: 7px;
          height: 100%;
          margin-left: 10px;
          vertical-align: middle;
          transition: 0.2s;
          transform:rotate(-180deg);
        }
      }
      &.open{
        .sub-menu{
          max-height:1000px;
        }
        & .active-item .arrow img {
          transform:rotate(0deg);
        }
      }
    }
  }
  .sub-menu {
    z-index: 700;
    list-style:none;
    max-height:0;
    overflow:hidden;
    position:relative;
    transition:.5s;
    li {
      cursor: pointer;
      color: $color-accent;
      padding: 6px 10px 10px 10px;
      border-top:1px solid $color-helper19;
      border-radius: unset;
      box-shadow: none;
      &:last-child {
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
      }
      &.active {
        display: none;
      }
    }
  }
}

.dropup {
  width: 300px;
  position: absolute;
  bottom: 15px;
  left: 90px;
  z-index: 10;
  .dropup-wrapper {
    position: relative;
  }
  .dropbtn {
    height: 50px;
    cursor: pointer;
    padding-left: 20px;
    background: #ffffff;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.08);
    color: $color-accent;
    position: relative;
    transition: .5s;
    border-radius: 5px;
    display: flex;
    align-items: center;
    .arrow {
      margin-left: auto;
      margin-right: 20px;
      img {
        transform: rotate(-180deg);
      }
    }
  }
  .dropup-content {
    max-height: 0;
    overflow: hidden;
    position: absolute;
    bottom: 48px;
    width: 100%;
    box-shadow: 0 -5px 8px 0 rgba(0, 0, 0, 0.08);
    transition:.3s;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    a {
      display: block;
      background-color: #fff;
      padding: 6px 10px 10px 20px;
      border-bottom: 1px solid #eeeeee;
    }
  }
}

.dropup.open .dropup-content {
  max-height: 1000px;
}
