.swiper-scrollbar {
  background: $color-helper3;
  .swiper-scrollbar-drag {
    background-color: $color-helper11;
    border-radius: unset;
  }
}

.os-scrollbar-handle,
.os-scrollbar.active {
  background-color: $color-helper11 !important;
  border-radius: unset !important;
}

.table-wrapper {
  .os-scrollbar-handle {
    background-color: $color-accent !important;
  }
}

.swiper-pagination-progressbar {
  background: rgba(0,0,0,.10);
}
.swiper-container-horizontal>.swiper-pagination-progressbar {
  margin-right: 15px;
  margin-left: 15px;
  border-radius: 5px;
  max-width: 100%;
  width: auto;
  top: unset;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background-color: $color-accent;
}
