.comment-block {
  display: none;
  .comment-block-wrapper {
    height: 100%;
    flex-direction: column;
    display: flex;
  }
  position: absolute;
  bottom: 15px;
  right: 13px;
  z-index: 500;
  max-height: 30px;
  width: 574px;
  background-color: $color-helper3;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.08);
  border-radius: 5px;
  .comment-head {
    width: 100%;
    height: 50px;
    padding: 15px 15px 20px 25px;
    h5 {
      margin: 0;
      font-size: 16px;
      @extend .text-regular;
    }
    .button-popup-close {
      bottom: -16px;
      right: 0;
      background-color: unset;
      box-shadow: unset;
    }
  }
  form {
    flex: 1;
    width: 100%;
    max-height: 0;
    background-color: $color-helper2;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    textarea {
      width: 100%;
      background-color: $color-helper2;
    }
    .textarea {
      transition: 300ms ease-out, 300ms ease-out;
      font-size: 16px;
      padding: 10px 20px 0 26px;
      &:focus {
        box-shadow: 0 0 3px 0 rgba(39, 168, 166, 0.36) inset !important;
      }
    }
  }
  &.open {
    max-height: 146px;
    display: block;
    form {
      max-height: 900px;
    }
  }
  &.is-save:after {
    display: block;
    content: 'Сохранено';
    position: absolute;
    font-size: 14px;
    color: $color-helper1;
    bottom: 15px;
    right: 20px;
  }
}
