.slider-nav {
  z-index: 300;
  position: fixed;
  top: 0;
  right: -410px;
  background-color: $color-helper3;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.08);
  height: 100vh;
  width: 338px;
  //overflow-x: hidden;
  transition: 300ms ease-out, 300ms ease-out;
  &.slider-form {
    width: 560px;
    right: -600px;
  }
}

.slider-nav.open {
  right: 0px;
}

.swiper-container-navigation {
  width: auto !important;
  height: 100vh !important;
  min-height: unset !important;
  max-height: unset !important;
  padding: 50px 24px 20px;
  .swiper-slide {
    overflow: hidden;
    cursor: pointer;
    width: 290px;
    height: 200px !important;
    border: 1px solid $color-helper19;
    border-radius: 5px;
    &.active-nav {
      border-color: $color-helper10;
    }
  }
}

.nav-block {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 200px;
  .nav-preview {
    display: block;
    width: 100%;
    height: auto;
    max-height: 160px;
  }
  .nav-footer {
    position: relative;
    border-top: 1px solid $color-helper19;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 7px 18px;
    h5, .nav-title {
      font-weight: normal;
      position: relative;
      margin: 0;
      font-size: 16px;
    }
    .number-nav {
      color: $color-helper1;
      padding-right: 7px;
    }
    &.have-commit:after {
      position: absolute;
      content: '';
      top: 0;
      right: 15px;
      height: 100%;
      width: 18px;
      background-image: url('../img/icon/ico-commit.png');
      background-repeat: no-repeat;
      background-position: center;
    }
  }
}
