.navigation-wrapper {
  max-width: 100%;
  position: relative;
  margin: 0 auto;
  .navigation {
    position: absolute;
    background-color: $color-helper3;
    height: 50px;
    width: 50px;
    border-radius: 5px;
    box-shadow: 0 0 8px 0 rgba(21, 21, 21, 0.1);
    cursor: pointer;
    outline: none;
    z-index: 10;
    img {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      user-select: none;
    }
  }
}

.button-prev img,
.button-next img{
  width: 9px;
  height: 19px;
}

.button-chat img {
  height: 20px;
  width: 19px;
}

.button-menu img {
  height: 13px;
  width: 18px;
}

.button-user img {
  height: 21px;
  width: 19px;
}

.button-logout img {
  height: 21px;
  width: 15px;
}

.button-next {
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
  bottom: 15px;
  right: 13px;
}

.button-prev {
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
  bottom: 15px;
  right: 64px;
}

.button-chat {
  bottom: 15px;
  right: 142px;
  .commit-exist {
    display: none;
  }
  &.exist {
    .commit-exist {
      display: block;
    }
    .commit-empty {
      display: none;
    }
  }
}

.button-return {
  bottom: 15px;
  left: 15px;
}

.button-logout {
  top: 15px;
  right: 13px;
}

.button-user {
  top: 15px;
  right: 76px;
}

.button-menu {
  top: 15px;
  right: 139px;
}

.navigation-wrapper {
  .button-close {
    background-color: unset;
    box-shadow: none;
    top: 0;
    left: -50px;
    img {
      height: 13px;
      width: 13px;
    }
  }
}

.navigation-nested {
  position: absolute;
  cursor: pointer;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  img {
    width: 22px;
    height: 42px;
  }
  &.nested-button-prev {
    left: 13px;
  }
  &.nested-button-next {
    right: 13px;
  }
}

.success-submit {
  display: none;
  position: absolute;
  text-align: center;
  width: 180px;
  z-index: 400;
  padding: 10px 30px 10px 40px;
  top: 70px;
  right: 15px;
  border-radius: 4px;
  color: $color-helper3;
  font-size: 14px;
  background-color: $color-helper5;
  background-image: url('../img/icon/ico-check.svg');
  background-repeat: no-repeat;
  background-position: 32px center;
  &.open {
    display: block;
  }
}

.navigation-top-fixed {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 20;
  width: 100vw;
}

.navigation-bottom-fixed {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100vw;
  z-index: 20;
}
