html,body {
  height: 100%;
  width: 100%;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.swiper-container {
  width: 100%;
  max-height: 100%;
}

.swiper-container-parent {
  height: 768px;
  width: 1024px;
}

.swiper-slide {
  position: relative;
}

.swiper-button-disabled:not(.navigation) {
  display: none !important;
}

.swiper-container-nested {
  position: relative;
}

.swiper-container-h-scroll {
  position: relative;
  &.scroll-end:before {
    z-index: 9;
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    height: 100%;
    width: 135px;
    background-image: url('../img/bg-right.png');
  }
  &.scroll-begin:after {
    z-index: 9;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 135px;
    background-image: url('../img/bg-left.png');
  }
}

.slider-img {
  .card {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  img {
    margin-right: 5px;
    max-width: 235px;
  }
}

.slider-advantages {
  .card-column {
    padding-top: 30px;
    .column {
      img {
        max-width: 178px;
      }
    }
  }
}

.slider-cards {
  .card {
    padding: 0;
  }
  .card-list {
    font-size: 14px;
    line-height: 18px;
  }
  .card-body {
    margin-top: 30px;
  }
  .col {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.slider-menu {
  .card-column {
    align-items: center;
    .card-title {
      margin-top: 0 !important;
    }
    p {
      margin: 0;
    }
  }
}

.slider-ppr {
  h1 {
    margin-bottom: 25px;
  }
}

.slider-card-single {
  .card .card-title {
    font-size: 16px;
    margin-bottom: 5px;
  }
  p {
    margin-top: 0;
    font-size: 14px;
    line-height: 18px;
  }
}

.slider-why-us {
  .card-column {
    align-items: center;
    .card-title {
      margin: 0;
    }
    p {
      margin: 0;
    }
  }
}

.slider-cards,
.slider-card-single {
  .col {
    padding: 0 20px;
  }
  .row {
    margin-left: -20px;
    margin-right: -20px;
  }
  .footnote {
    margin-left: 0;
  }
}

