* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.swiper-container-parent.scalled * {
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-font-smoothing: subpixel-antialiased;
}

html,body {
  margin: 0;
  font-size: 16px;
  min-width: 300px;
  @extend .text-light;
}

ol {
  counter-reset: item;
  list-style-type: none;
  padding-left: 32px;
  li:before {
    content: counter(item) "  ";
    counter-increment: item;
    margin-left: -32px;
    width: 32px;
    display: inline-block;
  }
}

h1 {
  @extend .text-regular;
  font-size: 36px;
  line-height: 46px;
  padding: 30px 30px 15px 30px;
  margin: 0;
}
h2 {
  @extend .text-bold;
  font-size: 24px;
  line-height: 30px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 20px 0 15px 0;
}

p {
  line-height: 23px;
}

a,
a:active,
a:focus,
a:hover {
  font-weight: normal;
  color: $color-helper7;
  text-decoration: none;
}

a:visited {
  color: $color-helper7;
  text-decoration: none;
}

.text-important {
  @extend .text-bold;
  color: $color-accent;
}

.text-small {
  font-size: 14px !important;
}

.bg-important {
  margin-top: 25px;
  margin-right: 30px;
  padding: 5px 30px;
  background-color: $color-helper8;
  color: $color-helper3;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}

.padding-v-15 {
  padding-top: 15px !important;
  padding-bottom: 15px !important;
}

.padding-0 {
  padding: 0 !important;
}

.margin-t-0 {
  margin-top: 0 !important;
}



.img-cover {
  width: 100%;
  height: 100%;
  //height: 100vh;
  //width: 100vw;
  height: 768px;
  background-size: cover;
  background-repeat: no-repeat;

}

.navigation-mobile-wrapper {
  display: none;
}

.mobile-nav {
  display: none;
}

.container {
  width: 100%;
  height: auto;

  .row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -30px;
    margin-left: -30px;
  }

  .col {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 30px;
    padding-left: 30px;
  }

  .col-10 {
    flex: 0 0 83.333333%;
    max-width: 83.333333%;
  }

  .card-column {
    margin: 0;
  }

  .col-8 {
    flex: 0 0 66.666667%;
    max-width: 66.666667%;
  }

  .col-7 {
    flex: 0 0 58.333333%;
    max-width: 58.333333%;
  }

  .col-6 {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .col-5 {
    flex: 0 0 41.666667%;
    max-width: 41.666667%;
  }

  .col-4 {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
  }

  .col-3 {
    flex: 0 0 25%;
    max-width: 25%;
  }

  .col-2 {
    flex: 0 0 16.666667%;
    max-width: 16.666667%;
  }

  .col-1 {
    flex: 0 0 8.333333%;
    max-width: 8.333333%;
  }

  .d-flex {
    display: flex;
  }

  .card-img {
    display: block;
    max-width: 100%;
    height: auto;
  }

}

.map {
  position: relative;
}

iframe {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 980px;
  min-height: 500px;
  &.loaded {
    display: block;
  }
}
